<template>
  <div id="nutrientDisplay">
    <div :style="{'opacity':showNuts?1:0}">
      <strong>{{getProtein}}g</strong> Protein
      <strong>{{getFat}}g</strong> Fat
      <strong>{{getCarbs}}g</strong> Carbs
      <strong>{{getKcal}}</strong> Kcal
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "NutrientDisplay",
  data() {
    return {
      showNuts: false
    };
  },
  computed: {
    ...mapGetters(["returnMealArray", "returnMealArrayLength"]),
    getProtein: function() {
      let i;
      let sumArray = [];
      let sum = 0;
      for (i = 0; i < this.returnMealArray.length; i++) {
        sumArray.push(this.returnMealArray[i].protein);
      }
      for (i = 0; i < sumArray.length; i++) {
        sum += sumArray[i];
      }
      return sum;
    },
    getFat: function() {
      let i;
      let sumArray = [];
      let sum = 0;
      for (i = 0; i < this.returnMealArray.length; i++) {
        sumArray.push(this.returnMealArray[i].fat);
      }
      for (i = 0; i < sumArray.length; i++) {
        sum += sumArray[i];
      }
      return sum;
    },
    getCarbs: function() {
      let i;
      let sumArray = [];
      let sum = 0;
      for (i = 0; i < this.returnMealArray.length; i++) {
        sumArray.push(this.returnMealArray[i].carbs);
      }
      for (i = 0; i < sumArray.length; i++) {
        sum += sumArray[i];
      }
      return sum;
    },
    getKcal: function() {
      let i;
      let sumArray = [];
      let sum = 0;
      for (i = 0; i < this.returnMealArray.length; i++) {
        sumArray.push(this.returnMealArray[i].kcal);
      }
      for (i = 0; i < sumArray.length; i++) {
        sum += sumArray[i];
      }
      return sum;
    }
  },
  watch: {
    returnMealArrayLength: function() {
      if (this.returnMealArrayLength == 0) {
        this.showNuts = false;
      } else {
        this.showNuts = true;
      }
    }
  }
};
</script>

<style scoped>
#nutrientDisplay {
  height: 22px;
  width: 500px;
  background-color: antiquewhite;
  border-style: solid;
  border-width: 2px;
  border-color: black;
  display: inline-block;
}
</style>
